.menu-item {
  background-image: url("../../images/background-black.jpg");
  background-size: cover;
  text-align: center;
  margin: 0 auto;
  padding: 1rem 0;
}

.heading {
  a {
    display: inline-block;
    text-decoration: none;
    font-weight: bold;
    font-size: 0.9rem;
    border-radius: 0.2rem;
    border: 2px solid $color_navajowhite;
    margin: 1rem;
    padding: 0.3rem 0.8rem;
    color: $color_gainsboro;

    &:hover {
      @include button-hover($color_black, $background_navajowhite);
    }
  }
}

.menu-subitems {
  @extend %display-grid-items;

  article {
    border: 1px solid $color_peru;

    &:hover {
      @extend %grid-items-hover;
    }

    img {
      @include grid-items-img-circle(10rem, 10rem);    
    }

    h3 {
      text-transform: uppercase;
      font-size: 1rem;
      padding-top: 2rem;
      color: $color_navajowhite;
    }

    p {
      font-size: 0.9rem;
      width: 80%;
      margin: 0 auto;
      color: $color_gray;

      &.price {
        font-weight: 700;
        padding-top: 1rem;
        color: $color_peru;
      }
    }
  }

  @extend %grid-menu-responsive;
}
