.about-us {
  background-image: url("../../images/background-black.jpg");
  background-size: cover;
  text-align: center;
  margin: 0 auto;
  padding: 1rem 0;

  .heading {
    .stars {
      text-align: center;
      margin-top: 3rem;

      i {
        font-size: 2rem;
        color: $color_navajowhite;
      }
    }
  }

  .story {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 3rem auto;

    .story-items {
      .subheading {
        h4 {
          padding-top: 0.5rem;
          color: $color_navajowhite;
        }

        p {
          text-align: left;
          width: 70%;
          margin: 0 auto;
          color: $color_gainsboro;

          @include media-query(phone-wide, max) {
            width: 80%;
          }
        }
      }

      @include media-query(tablet, max) {
        order: 1;
      }
    }

    .story-img {
      background: url("../../images/about.jpg");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      min-height: 20rem;

      @include media-query(tablet, max) {
        order: 2;
      }
    }

    @include media-query(tablet, max) {
      grid-template-columns: repeat(1, 1fr);
      margin: 0 auto 2rem auto;
    }
  }

  .chefs {
    .subheading {
      h4 {
        color: $color_navajowhite;
        padding-bottom: 0.3rem;
      }

      p {
        color: $color_gainsboro;
      }
    }

    .chefs-items {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 2rem;
      width: 90%;
      margin: 1rem auto;

      article {
        padding: 0.5rem;
        border: 1px solid $color_peru;

        &:hover {
          @extend %grid-items-hover;
        }

        img {
          @include grid-items-img-circle(8rem, 8rem);
        }

        h5 {
          text-transform: uppercase;
          font-size: 1rem;
          padding-top: 1rem;
          padding-bottom: 0.3rem;
          color: $color_navajowhite;
        }

        p {
          width: 80%;
          font-size: 0.9rem;
          margin: 0 auto;
          color: $color_gray;
        }

        p.job {
          font-size: 1rem;
          color: $color_gainsboro;
        }
      }

      @include media-query(tablet, max) {
        grid-template-columns: repeat(2, 1fr);
        width: 80%;
      }

      @include media-query(phone-wide, max) {
        grid-template-columns: repeat(1, 1fr);
        width: 70%;
      }
    }
  }
}
